import { CSSProperties, DetailedHTMLProps, ReactNode } from "react";

export type TButtonAttributes = DetailedHTMLProps<React.ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement>

export const Button = ({ children, css, handle }: { children: ReactNode, css?: CSSProperties, handle: (e:any) => void }) => {
  return (
    <button
      onClick={handle}
      className="button focus-within:m-auto md:m-0 w-full md:w-auto md:px-6 font-bold text-sm py-3 flex justify-center items-center rounded-lg text-white border-0"
      style={{ ...css }}>
      {children}
    </button>
  );
}