import { CSSProperties, DetailedHTMLProps } from "react";
import { activeColor } from "../theme";

export type TButtonAttributes = DetailedHTMLProps<React.ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement>

export const hashClick = (e?: any, id?: string) => {
  e && e.preventDefault();
  if (!id) return
  const element = document.getElementById(id);
  element && element.scrollIntoView({ block: 'start', behavior: 'smooth' });
}

export const LinkTo = ({ text, css, href, id }: { text?: string, css?: CSSProperties, href?: string, id?: string }) => {
  return (
    <a className="font-bold text-xl md:text-base transition duration-500 ease-in-out transform hover:scale-110 cursor-pointer" href={href} onClick={(e) => {
      id && hashClick(e, id)
    }} style={{ color: activeColor, ...css }} target="_blank" rel="noopener noreferrer">
      {text || "Learn More"}<svg className="inline ml-2" width="9" height="16" viewBox="0 0 9 16" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M1.35355 0.646752C1.15829 0.45149 0.841709 0.45149 0.646447 0.646752C0.451184 0.842014 0.451184 1.1586 0.646447 1.35386L1.35355 0.646752ZM8 8.00031L8.35355 8.35386C8.54882 8.1586 8.54882 7.84201 8.35355 7.64675L8 8.00031ZM0.646447 14.6468C0.451184 14.842 0.451184 15.1586 0.646447 15.3539C0.841709 15.5491 1.15829 15.5491 1.35355 15.3539L0.646447 14.6468ZM0.646447 1.35386L7.64645 8.35386L8.35355 7.64675L1.35355 0.646752L0.646447 1.35386ZM7.64645 7.64675L0.646447 14.6468L1.35355 15.3539L8.35355 8.35386L7.64645 7.64675Z" fill="#F44527" />
      </svg>
    </a>
  );
}