import { BodyBox } from "./BodyBox"
import { useMemo, useRef } from "react"

export const AskCookie = () => {
  const ref = useRef<HTMLDivElement>(null);

  const removeDiv = () => {
    const dom = ref.current;
    if (!dom) {
      return;
    }
    dom?.remove();
  }

  const reject = () => {
    setCookie("agreeCookie", "false", 30);
    removeDiv();
  }

  const agree = () => {
    setCookie("agreeCookie", "true", 365);
    removeDiv();
  }

  const setCookie = (key: string, value: string, days: any) => {
    const date = new Date();
    date.setDate(date.getDate() + days);
    const expires = "expires=" + date.toUTCString(); //根据世界时 (UTC) 把 Date 对象转换为字符串
    document.cookie = key + "=" + value + "; " + expires;
  }

  const getCookie = (key: string) => {
    const cookieArr = document.cookie.split(";");
    for (let index = 0, length = cookieArr.length; index < length; index++) {
      const cookieStr = cookieArr[index].trim();
      if (cookieStr.indexOf(key + "=") === 0) {
        return cookieStr.substring(key.length + 1, cookieStr.length);
      }
    }
    return "";
  }

  const checkCookie = useMemo(() => {
    const value = getCookie("agreeCookie");
    return value !== null && value.length > 0;
  }, [])

  if (checkCookie) return null

  return (
    <div className="bg bg-black z-50 fixed w-full h-60 md:h-40 text-white flex items-center" ref={ref} style={{ bottom: 0 }}>
      <BodyBox>
        <div className="flex flex-wrap md:justify-between items-center">
          <div className="w-full md:w-3/5">
            <div>Moledao uses cookies to improve your experience on our site and to show</div>
            <div>your relevant event,advertising and courses.</div>
            <br />
            <div>To find out more,please read our
              <a href="./policy/Privacy Policy.pdf" target="_blank"><span style={{ color: " #F2664F" }}> Privacy Policy</span></a> and
              <a href="./policy/Cookie Policy.pdf" target="_blank"><span style={{ color: " #F2664F" }}> Cookie Policy</span></a>
            </div>
          </div>
          <div className="w-full md:w-2/5 mt-6 md:mt-0">
            <button onClick={reject}>Opt-Out Cookies</button>
            <button className="ml-10 rounded-full bg bg-red-500 px-10 py-2" onClick={agree}>I Agree</button>
          </div>
        </div>
      </BodyBox>
    </div>
  )
}