export const Journey = () => {
  const ref = useRef<HTMLDivElement>(null);
  let timer: any;

  useEffect(() => {
    const dom = ref?.current;
    if (!dom) return
    const width = dom.scrollWidth / data?.length;
    dom.scrollLeft = width * 3
  }, [ref])

  return (
    <Background img={bgImg} img2={bgImg2}>
      <div id="journey" className="absolute -top-32 left-0 w-full">
        <BodyBox>
          <h1 className="font-black text-5xl md:text-6xl font-Poppins-bold">Our Journey</h1>
          <p className="pt-6 pb-4 text-xl">
            Since our establishment, we have been working tirelessly to empower our community. To realise our future endeavours, this is our roadmap.
          </p>
          <div className="flex items-center">
            <img className="cursor-pointer transform rotate-180" src={Vector} alt="" onClick={() => move(ref, data, timer)} />
            <div className="overflow-scroll relative h-96 w-full mx-2 pl-4" ref={ref}>
              <div className="flex absolute top-0">
                {data.map((item, i) => {
                  return (
                    <div key={i} className="">
                      <div className="ml-4 h-80 w-40 pb-10">
                        {<div style={{ height: i % 2 === 0 ? 0 : 50 }}></div>}
                        <img src={item?.icon} alt="" className="w-72 h-auto m-auto" />
                        <div className="border-l-2 pl-2" style={{ borderColor: "#BCC1CB", height: i === 0 ? 110 : (i % 2 === 0 ? 150 : 100) }}>
                          <h1 className="font-black font-Poppins-bold text-xl md:mt-4 mt-2" style={{}}>{item.title}</h1>
                          <p className="font-black" style={{ color: secondColor }}>{item?.desc}</p>
                        </div>
                      </div>
                      <div className="flex items-center -ml-1">
                        <img src={i === 0 ? startup : (item?.completed ? completed : Incompleted)} style={{ marginTop: i === 0 ? -40 : 0, marginLeft: i === 0 ? -11 : 0 }} alt="" />
                        {i !== 0 && <div className="h-1 w-full" style={{ background: `linear-gradient(90deg, ${item?.completed ? "#F8AFA2" : "#BCC1CB"} -13.84%, ${item?.completed ? "#FDEDEA" : "#F2F4F8"} 94.72%)` }}></div>}
                      </div>
                    </div>)
                })}
              </div>
            </div>
            <img className="cursor-pointer" src={Vector} alt="" onClick={() => move(ref, data, timer, true)} />
          </div>
        </BodyBox>
      </div>
    </Background>
  );
};