import { useHistory } from "react-router";
import { useLocation } from "react-router-dom";
import { memo, useCallback, useState } from "react";
import logo from "../assets/image/home/logo.png"
import { activeColor, textColor, secondColor } from "../theme";
import { useTranslation } from "react-i18next";
import { DropDown } from "./DropDown";
import { useSwitch } from "./Loading";

export const nav = [
  { label: "Home", path: "/home" },
  { label: "Mole Gather", path: "/gather" },
  { label: "Contact Us", path: "/contact" },
]

export const ArrowImg = ({ active }: { active?: boolean }) => {
  return (
    <svg width="9" height="16" viewBox="0 0 9 16" fill="none" xmlns="http://www.w3.org/2000/svg" className={`cursor-pointer transition-all duration-500 transform ${active ? "-rotate-90" : "rotate-90"}`} style={{ marginBottom: 5, marginLeft: 8 }}>
      <path fillRule="evenodd" clipRule="evenodd" d="M0.292893 0.292893C0.683417 -0.0976311 1.31658 -0.0976311 1.70711 0.292893L8.70711 7.29289C9.09763 7.68342 9.09763 8.31658 8.70711 8.70711L1.70711 15.7071C1.31658 16.0976 0.683417 16.0976 0.292893 15.7071C-0.0976311 15.3166 -0.0976311 14.6834 0.292893 14.2929L6.58579 8L0.292893 1.70711C-0.0976311 1.31658 -0.0976311 0.683417 0.292893 0.292893Z" fill={active ? textColor : secondColor} />
    </svg>
  )
}

