const Desc = ({ title }: { title: string }) => {
  return <h1 className="font-bold text-xs" style={{ color: secondColor }}>{title}</h1>
}

const Link = ({ text, href, close, mb, ml }: { text: string, href: string, close: () => void, mb?: number, ml?: number }) => {
  const history = useHistory();
  const { pathname } = useLocation();
  let isActive = pathname?.includes(href);

  return <p className={`font-bold text-base cursor-pointer hover:text-red-500 md:text-base`} style={{ marginBottom: mb || 15, marginLeft: ml || 0, color: isActive ? activeColor : textColor }} onClick={() => {
    if (href?.includes("http")) {
      window.open(href, "_blank");
    } else {
      history.push(href);
    }
    close();
  }}>{text}</p>
}

const SelectContactUs = ({ isOpen, close }: { isOpen: boolean, close: () => void }) => {
  return (
    <DropDown isOpen={isOpen}>
      <div className="p-4" style={{ color: secondColor }}>
        <Link text="Brand Assets" href="contact-welcome" close={close} mb={3} />
        <Desc title="Download Moledao logo & assets" />
        <div className="h-2"></div>
        <Link text="Careers at moledao" href="contact-careers" close={close} mb={3} />
        <Desc title="Explore open roles Moledao" />

        {/* <Title title="FAQ" />
        <Link text="Visit our FAQ for answers to common questions or contact us directly" href="contact-faq" close={close} mb={1}/> */}
      </div>
    </DropDown>
  )
}

export const useQueryParams = () => {
  const { search } = useLocation();
  const parmasString = search.substring(1);
  const params = new URLSearchParams(parmasString);
  return params
}

export const Header = memo(() => {
  const history = useHistory()
  const { pathname } = useLocation();
  const [open, setOpen] = useState<boolean>();
  const { t } = useTranslation();
  const [isOpen, ope, close] = useSwitch();

  const backTop = () => {
    document.documentElement.scrollTop = 0;
    document.body.scrollTop = 0;
  }

  const goPath = useCallback((path: string) => {
    history.push(`${path}`);
    backTop();
  }, [history])

  return (
    <div className="flex px-4 md:px-20 items-center justify-between text-l fixed w-full h-16 md:h-20 bg-gray-50 md:bg-opacity-90 text-black text-opacity-70 shadow-sm" style={{ zIndex: 200 }}>
      <img className="cursor-pointer mr-20 w-32 lg:w-40 absolute md:z-50 z-0" src={logo} onClick={() => {
        setOpen(false);
        goPath("/home")
      }} alt="logo" />
      <div className="md:hidden absolute right-4 cursor-pointer h-16 flex items-center" onClick={() => setOpen(!open)}>
        {!open ? <svg width="25" height="12" viewBox="0 0 25 12" fill="none" xmlns="http://www.w3.org/2000/svg">
          <rect x="0.880859" width="24" height="2" fill="#25282D" />
          <rect x="0.880859" y="5" width="24" height="2" fill="#25282D" />
          <rect x="0.880859" y="10" width="24" height="2" fill="#25282D" />
        </svg> :
          <svg width="28" height="28" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg">
            <rect x="7.75488" y="6.61963" width="19.2688" height="1.60573" rx="0.802867" transform="rotate(45 7.75488 6.61963)" fill="#25282D" />
            <rect x="6.62012" y="20.2451" width="19.2688" height="1.60573" rx="0.802867" transform="rotate(-45 6.62012 20.2451)" fill="#25282D" />
          </svg>}
      </div>
      <div onMouseLeave={close} className={`${open ? "black" : "hidden"} hidden md:flex h-20 bg-white md:pb-0 md:bg-transparent absolute top-16 left-0 flex-col w-full pr-20 md:top-0 md:flex-row items-center md:justify-end md:flex-1 font-medium text-lg shadow-lg md:shadow-none overflow-hidden`}>
        {nav.map(item => {
          let isActive = pathname?.includes(item?.path);
          return (<div
            key={item?.label}
            style={{ color: isActive ? textColor : secondColor, borderColor: isActive ? activeColor : "transparent" }}
            className="nav cursor-pointer ml-14 h-10 mt-4 md:mt-0 border-b-2 flex items-center font-bold"
            onClick={() => {
              setOpen(false);
              if (item?.label === "Contact Us") {
                isOpen ? close() : ope();
              } else {
                close();
                goPath(item?.path);
              }
            }}
          >{t(`common.${item?.label}`)}{item?.label === "Contact Us" && <ArrowImg active={isActive || isOpen} />}
            <SelectContactUs isOpen={isOpen} close={close} /></div>)
        })}
      </div >

      <div onMouseLeave={close} className={`${open ? "block" : "hidden"} md:hidden h-screen transition ease-in-out bg-white absolute top-16 left-0 w-full font-medium text-lg shadow-lg`}>
        {nav.map((item, i) => {
          let isActive = pathname?.includes(item?.path);
          return (<div
            key={item?.label}
            style={{ color: isActive ? activeColor : textColor, borderColor: i === 0 ? "transparent" : "#DFE4EA" }}
            className="h-14 pl-4 border-t flex items-center font-bold"
            onClick={() => {
              if (item?.label === "Contact Us") return
              setOpen(false);
              goPath(item?.path);
            }}
          >{t(`common.${item?.label}`)}</div>)
        })}
        <div>
          <Link text="Brand Assets" href="contact-welcome" close={() => { setOpen(false) }} ml={30} />
          <Link text="Careers" href="contact-careers" close={() => { setOpen(false) }} ml={30} />
          <Link text="Work at Moledao" href="contact-work" close={() => { setOpen(false) }} ml={50} />
        </div>
      </div >
    </div>
  );
});