import { CSSProperties } from "react";
import { activeColor } from "../theme";

export const RedLine = () => <span className="inline-block w-12" style={{ background: activeColor, height: 2 }}></span>;

export const LongLine = ({ css }: { css?: CSSProperties }) => <div className="w-3/5" style={{ background: "#f2f4f8", height: 1, ...css }}></div>;

