import bgImg from "../../assets/image/desktop/0 Home/Desktop-Landing - Background (1440x2048px).png"
import bgImg1 from "../../assets/image/desktop/0 Home/Landing - Mole Cave Background.png"
import bgImg2 from "../../assets/image/mobile/0 Home/Mobile-Landing - Mole Cave Background.b8351390.png"
import { BodyBox } from "../../components/BodyBox";
import { Button } from "../../components/Button";
import { mainColor, activeColor, textColor, secondColor } from "../../theme";
import { useTranslation } from "react-i18next";
import { Background } from "../../components/Background";
import Island from "../../assets/image/desktop/0 Home/Island (1440x2048px).webm"
import Island2 from "../../assets/image/mobile/0 Home/(0 Home) Island (320x1745px).webm"
import buidlGraphic from "../../assets/image/mobile/0 Home/imagePlaceholder.png"
import growGraphic from "../../assets/image/mobile/0 Home/imagePlaceholder (1).png"
import empowerGraphic from "../../assets/image/mobile/0 Home/imagePlaceholder (2).png"
import { useEffect, useRef, useState } from "react";
import { getBrowserType } from "../../helpers/utilities";

const data = [
  {
    title: "build",
    icon: buidlGraphic
  },
  {
    title: "grow",
    icon: growGraphic
  },
  {
    title: "empower",
    icon: empowerGraphic
  }
]

const Vision = ({ item }: { item: { title: string, icon: string } }) => {
  const { t } = useTranslation();
  return (
    <div className="w-full shadow-lg md:shadow-none p-2 md:p-0">
      <img src={item?.icon} alt="" className="w-5/6 md:w-80 h-auto m-auto" />
      <h1 className="font-semibold text-3xl md:mt-6 mt-2" style={{ color: mainColor }}>{t(`about.${item.title}`)}</h1>
      <p className="h-44" style={{ color: "#6B6B6B" }}>{t(`about.${item.title}Desc`)}</p>
    </div>
  )
}

export const First = () => {
  const { t } = useTranslation();
  const isChorme = getBrowserType();
  const [currentTab, setTab] = useState(0);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const dom = ref.current;
    if (!dom) return
    let timer: NodeJS.Timeout | null = null;
    const width = dom.scrollWidth / data?.length;
    dom.addEventListener("scroll", (e) => {
      if (timer) return
      timer = setTimeout(() => {
        const index = Math.floor((dom.scrollLeft + width / 2) / width);
        setTab(index);
        clearTimeout(timer as any);
        timer = null
      }, 30)
    })
  }, [setTab])

  return (
    <Background img={isChorme === "Chrome" ? bgImg : bgImg1} img2={bgImg2} css={{ minHeight: "200vh" }}>
      {isChorme === "Chrome" && <div className="video absolute top-0 md:-top-24 left-0 w-full">
        <video className="hidden md:block" width="100%" autoPlay loop muted>
          <source src={Island} type="video/webm" />
        </video>
        <video className="md:hidden block border" width="100%" autoPlay loop muted>
          <source src={Island2} type="video/webm" />
        </video>
      </div>}
      <div className="absolute top-10 sm:top-20 md:top-28 lg:top-40 xl:top-60 2xl:top-96 left-0 w-full">
        <BodyBox>
          <h1 className="font-bold font-Poppins-bold text-6xl md:text-7xl my-8 mt-16" style={{ color: mainColor, letterSpacing: -2 }}>{t("home.build")}.  <br />{t("home.grow")}. <br />{t("home.empower")}</h1>
          <p className="font-bold font-Poppins-bold text-lg md:text-xl mb-10 mt-10 md:w-2/5 w-80" style={{ color: "#ffffff" }}>
            A community to connect and develop budding entrepreneurs in this space, facilitating collaborative efforts towards building the Web 3 ecosystem.
          </p>
          <Button handle={() => {
            window.open('https://discord.com/invite/5m4w49uXJg', "_blank");
          }}>
            Join our community to find out more
          </Button>
        </BodyBox>
      </div>

      <div className="absolute bottom-60 sm:bottom-10 left-0 w-full md:bottom-10 lg:bottom-0 xl:bottom-40">
        <BodyBox>
          <h1 className="font-black text-5xl md:text-6xl font-Poppins-bold" style={{ letterSpacing: -2 }}>Our Vision</h1>
          <p className="pt-6 pb-4">
            <span className="font-black font-Poppins-bold" style={{ color: activeColor }}>Vision: </span><br className="block md:hidden" />
            "We believe that all individuals have the right to be empowered and have their potential realised and maximised. Having a lack of connections and resources should not stop anyone from realising their aspirations. In so doing, we can benefit the blockchain space."
          </p>
          <p className="">
            <span className="font-black font-Poppins-bold" style={{ color: activeColor }}>Mission </span><br className="block md:hidden" />
            To realise our vision, we have built an interactive platform for blockchain enthusiasts to gain access to resources and build meaningful connections with like-minded individuals.
          </p>
          <div className="flex justify-start md:justify-around pb-0 sm:pb-10 md:pb-20 lg:pb-30 xl:pb-0 2xl:pb-60 z-50 mt-10 overflow-scroll" ref={ref}>
            {data?.map((item, i) => {
              return (
                <div key={i}>
                  <div className="hidden md:block text-center text-lg md:pb-0 pb-10 w-80"><Vision item={item} /></div>
                  <div className={`md:hidden block text-center text-lg md:pb-0 pb-10 pt-4 m-6 ${currentTab === i ? "transition-all duration-500 ease-in-out transform scale-110" : ""}`}
                    style={{
                      width: "calc(100vw - 100px)",
                      minWidth: "calc(100vw - 100px)",
                    }}>
                    <Vision item={item} />
                  </div>
                </div>
              )
            })}
          </div>
        </BodyBox>
        <div className="md:hidden flex justify-center">
          {data?.map((_, i) => {
            return (
              <div key={i} className="w-2 h-2 rounded-full mx-2" style={{ background: currentTab === i ? textColor : secondColor }}></div>
            )
          })}
        </div>
      </div>
    </Background>
  );
};