export const Partners = () => {
  const { searchString, setSearchString } = useQueryString();
  const select = useMemo(() => {
    const item = tabs?.find(item => item?.label === searchString?.tab);
    if (item && item?.label !== "updates") return item
    else return tabs[0]
  }, [searchString.tab]);

  useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.body.scrollTop = 0;
  }, [])

  return (
    <div className="py-20" style={{ background: "#F2F4F8", borderBottom: "5px solid #FFFFFF" }}>
      <BodyBox >
        <h1 className="font-black text-5xl md:text-6xl font-Poppins-bold">Our Partners</h1>
        <p className="py-4 text-xl">
          Interested in becoming our partner?<br />
          <span className="underline cursor-pointer" onClick={() => {
            window.open("https://docs.google.com/forms/d/1rEKzjkgF3AvPeJqxr_7xpM2OkRdsOgObNz9cpJWr8gM/edit", "_blank")
          }} style={{ color: activeColor }}>Submit details here</span> and we’ll be in touch when opportunities are open.
        </p>
        <div className="mt-10 flex items-center justify-between font-medium text-lg mb-8 overflow-scroll">
          {tabs.map(item => {
            const isActive = (searchString.tab || "Diamond") === item?.label;
            return (<div
              key={item?.label}
              style={{ color: isActive ? textColor : secondColor, borderColor: isActive ? activeColor : "#f2f4f8" }}
              className="font-black cursor-pointer h-12 border-b-2 flex items-center md:w-60 md:justify-center px-4"
              onClick={() => {
                setSearchString("/home", { tab: item?.label })
              }}
            >{item?.label} </div>)
          })}
        </div>

        <div className="hidden md:flex flex-wrap justify-center mb-8">
          {select?.data?.map((item, i) => {
            return (
              <a key={item?.title + "" + i} className={`overflow-hidden w-64 m-4 rounded-md`} target="_blank" href={item?.link?.website} rel="noreferrer">
                <img src={item?.image} className="w-full transition duration-500 ease-in-out transform hover:scale-125" alt="" />
              </a>
            )
          })}
        </div>
      </BodyBox>

      <LongLine css={{ width: "100%", background: "#BCC1CB" }} />

      <BodyBox >
        <div className="flex flex-wrap justify-between">
          {select?.data?.map((item, i) => {
            return (
              <div key={item?.title + "-" + i} className={`mb-6 border-t pt-8 flex flex-wrap ${searchString?.tab === "Gold" ? "w-full md:w-5/12" : "w-full"}`} style={{ borderColor: i === 0 || searchString?.tab === "Gold" ? 'transparent' : "#BCC1CB" }}>
                <a className={`mb-4 w-full ${searchString?.tab === "Gold" ? "h-20 bg-white" : "h-56 md:w-72 mr-0 md:mr-8"} overflow-hidden rounded-md`} target="_blank" href={item?.link?.website} rel="noreferrer">
                  <img src={item?.image} className={`${searchString?.tab === "Gold" ? "h-40 m-auto transform scale-150 hover:scale-175" : "w-full hover:scale-125"} h-full transition duration-500 ease-in-out transform`} alt="" />
                </a>
                <div className="flex-1">
                  <p className="text-sx transform scale-y-75" style={{ color: "#BCC1CB" }}>{item?.subTitle?.toUpperCase()}</p>
                  <div className="text-3xl font-black mb-2">{item?.title}</div>
                  <p style={{ color: secondColor }}>{item?.desc}</p>
                  <div className="flex mt-4">
                    {Object.keys(item?.link).map((ite: any, index) => {
                      return (<a className="mr-2 transition duration-500 ease-in-out transform hover:scale-125" target="_blank" rel="noreferrer" key={index} href={(item?.link as any)[ite]}>
                        {((imgs as any)[ite] as any)()}
                      </a>)
                    })}
                  </div>
                </div>
              </div>
            )
          })}
        </div>
      </BodyBox>
    </div>
  );
};