export const DropDown = ({ children, isOpen }: { children: ReactNode, isOpen: boolean }) => {

    return (
        isOpen ? <div className="fixed right-10 top-20 w-52 bg-white shadow-2xl rounded-md cursor-default"
        onClick={(e) => {
                e.stopPropagation();
            }}
        >
            <div className="select -top-4 right-8 shadow-2xl"></div>
            { children }
        </div> : null
    )
}