import { CSSProperties, ReactNode } from "react";

export const Background = ({ img, img2, children, css }: { children: ReactNode, img: string, img2?: string, css?: CSSProperties }) => {

  return (
    <div className="relative min-h-screen" style={css}>
      {children}
      <img className={`${img2 ? "hidden md:block" : "block"} w-full h-auto`} src={img} alt="" />
      { img2 && <img className={`block md:hidden w-full h-auto`} src={img2} alt="" /> }
    </div>
  );
};