import { Route, Switch } from "react-router-dom"
import { Citizen } from "../pages/contact/Citizen"
import { FAQ } from "../pages/contact/FAQ"
import { Wellcome } from "../pages/contact/Wellcome"
import { Work } from "../pages/contact/Work"
//import { Education } from "../pages/education"
// import { EducationDetails } from "../pages/education/EducationDetails"
// import { EducationList } from "../pages/education/EducationList"
import { Gather } from "../pages/gather"
import { Home } from "../pages/home"

export const routes = [
  /* { path: "/education", component: Education },
  { path: "/education-details/:id", component: EducationDetails },
  { path: "/education-list", component: EducationList }, */
  { path: "/gather", component: Gather },
  { path: "/contact-welcome", component: Wellcome },
  { path: "/contact-careers", component: Citizen },
  { path: "/contact-work", component: Work },
  { path: "/contact-faq", component: FAQ },
  { path: "/home", component: Home },
]

export const Routes = () => {
  return (
    <Switch>
      {routes?.map(item => <Route key={item.path} path={item?.path} component={item.component} />)}
      <Route exact component={Home} />
    </Switch>
  )
}