const Question = ({ item }: { item: { question: string, answer: string } }) => {
  const [isOpen, open, close] = useSwitch()
  return (
    <div className="border-b pt-6" style={{ borderColor: "#BCC1CB" }}>
      <div className="flex justify-between cursor-pointer" onClick={() => {
        isOpen ? close() : open()
      }} >{item?.question}
        <ArrowImg active={isOpen} />
      </div>
      {<div className={`transition-transform px-4 pt-4 overflow-hidden`} style={{
        transition: "height .5s",
        height: isOpen ? 100 : 0
      }}>{item?.answer}</div>}
    </div>
  )
}

